import Vue from 'vue'
import Vuex from 'vuex'
// API
import { songDetail , songUrl , songLyric , songSimi , songComment  } from '../common/api.js';

Vue.use(Vuex);
// new Vuex 
export default new Vuex.Store({
	// 
    state : {
		login: false,  
		token: '',  
		avatarUrl: '',  
		userName: '' ,
        topIdList : [],// 
		nextId : '',// iID
		// 
		songDetail : {
			al : { picUrl : '' },
			ar : { name : '' }
		},
		playicon : 'icon-suspend_icon',// 
		isplayrotate : true,// 
    },
    mutations : {
		login(state, provider) {  
		    console.log(state)  
			// 
			console.log(provider)  
		    state.login = true;  
		    state.token = provider.token;  
		    state.nickName = provider.profile.nickname;  
		    state.avatarUrl = provider.profile.avatarUrl;  
		},  
		logout(state) {  
		    state.login = false;  
		    state.token = '';  
		    state.nickName = '';  
		    state.avatarUrl = '';  
		},
		// 
		setPlayList(state, value){
			console.log('')
		},
		// ID
        INIT_CHANGE(state,payload){
            state.topIdList = payload;
        },
		// ID
		NEXT_ID(state,payload){
			// ID
			for(var i=0;i<state.topIdList.length;i++){
				// id ID
				if( state.topIdList[i].id == payload ){
					// ID
					state.nextId = state.topIdList[i+1].id;
				}
			}
		}
    }
});